.nav {
  text-transform: uppercase;
  font-size: 14px;
}

.nav-link {
  vertical-align: top;
  color: #fff;
  margin: 0 15px;
  text-decoration: none;
  transition: color .1s linear;
  display: inline-block;
  position: relative;
}

.nav-link:after {
  content: "";
  width: 100%;
  height: 3px;
  opacity: 0;
  z-index: 1;
  background-color: #fce38a;
  transition: opacity .1s linear;
  display: block;
  position: absolute;
  top: 100%;
  left: 0;
}

.nav-link:hover {
  color: #fce38a;
}

.nav-link:hover:after, .nav-link.active:after {
  opacity: 1;
}

.nav-link.active {
  color: #fce38a;
}

.header {
  width: 100%;
